.image-container {
    box-shadow: inset 0px 0px 50vw 4vw rgba(0, 0, 0, 0.8);
    height: 100vh;
}

.image-intro {
    object-fit: cover;
    max-width: 100%;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: -1;

    /* Disable selection */
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
}

.over-container {
    height: 100vh;
    top: 0;
}

.over-text-container {
    position: absolute;
    top: 20%;
    width: 100%;
}

.over-text {
    color: #f5f5f5;
    font-family: 'Archivo Black', 'Merriweather Sans', sans-serif;
}

.name-text {
    font-size: 5.5vw;
    letter-spacing: 13px;
    -webkit-animation: 1s ease-out 0s 1 name-text-on-load;
            animation: 1s ease-out 0s 1 name-text-on-load;
    margin-bottom: 3vh;
}

.job-text {
    font-size: 3.5vw;
    letter-spacing: 10px;
    -webkit-animation: 1s ease-out 0s 1 job-text-on-load;
            animation: 1s ease-out 0s 1 job-text-on-load;
}

@-webkit-keyframes name-text-on-load {
    0% {
        opacity: 0.5;
        -webkit-transform: translateY(5vh);
                transform: translateY(5vh);
    }

    100% {
        opacity: 1;
        /* transform: translateY(0); */
    }
}

@keyframes name-text-on-load {
    0% {
        opacity: 0.5;
        -webkit-transform: translateY(5vh);
                transform: translateY(5vh);
    }

    100% {
        opacity: 1;
        /* transform: translateY(0); */
    }
}

@-webkit-keyframes job-text-on-load {
    0% {
        opacity: 0;
        -webkit-transform: translateY(7vh);
                transform: translateY(7vh);
    }

    100% {
        opacity: 1;
        /* transform: translateY(0); */
    }
}

@keyframes job-text-on-load {
    0% {
        opacity: 0;
        -webkit-transform: translateY(7vh);
                transform: translateY(7vh);
    }

    100% {
        opacity: 1;
        /* transform: translateY(0); */
    }
}

.over-arrow-container {
    position: absolute;
    width: 100vw;
    bottom: 3%;
}

.arrow-button {
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    cursor: pointer;
}

.arrow-button:focus, button:hover{
    outline: none;
}

.arrow-image {
    height: 5vh;
    width: auto;
    -webkit-filter: invert(98%) sepia(89%) saturate(196%) hue-rotate(209deg) brightness(115%) contrast(92%);
            filter: invert(98%) sepia(89%) saturate(196%) hue-rotate(209deg) brightness(115%) contrast(92%);
    -webkit-animation: 1s ease-out 0s infinite arrow-down-loop;
            animation: 1s ease-out 0s infinite arrow-down-loop;
}

@-webkit-keyframes arrow-down-loop {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    50% {
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }

    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes arrow-down-loop {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    50% {
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }

    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

